import React from 'react';

export type TriggerFormulaProps = {
    formulas: string[]
    addFormula: () => void,
    updateFormula: (idx: number, updatedFormula: string) => void,
    removeFormula: (idx: number) => void, 
  }

export class TriggerFormulas extends React.Component<TriggerFormulaProps, {}> {  

    constructor(props: TriggerFormulaProps) {
        super(props)
    }

    _onClick() {
        console.log('calling here')
        this.props.addFormula()
    }
    
    renderInput(formula: string, idx: number): JSX.Element {
    return (<div className='mt-2 flex flex-row align-center content-center justif-center'>
        <input onChange={(e) => this.props.updateFormula(idx, e.target.value)} className='rounded-xl p-1 mr-2 bg-blue-100' value={formula}></input>
        <div key={idx} data-key={idx} onClick={(e => this.props.removeFormula(idx))} className='m-auto cursor-pointer bg-black text-white w-6 h-6 flex justify-center content-center items-center rounded-2xl'>X</div>
    </div>)
    }

    render(): JSX.Element {
        return (<div>
        {this.props.formulas.map((formula, idx) =>{
         return (<div key={idx}>{this.renderInput(formula, idx)}</div>)
        })
        }
        <div onClick={this._onClick.bind(this)} className='mt-3 ml-auto cursor-pointer bg-lime-900 text-white w-6 h-6 flex justify-center content-center items-center rounded-2xl font-bold'>+</div>
        </div>
        )
    }
}