/**
 * Return a map associating from key values to items
 * Warning: Javascript's Map uses object equality, e.g. Date(0) != Date(0)
 * @param items Items to group
 * @param keyFunc Key function to group by
 */
export function groupByKey<T, K>(items: T[], keyFunc: (item: T) => K): Map<K, T[]> {
    const result: Map<K, T[]> = new Map()
    for (const item of items) {
        const keyValue = keyFunc(item)
        if (result.has(keyValue)) result.get(keyValue)?.push(item)
        else result.set(keyValue, [item])
    }
    return result
}