import React, { Fragment, ReactElement, RefObject } from "react";

import { MS_TO_SEC, SEC_TO_MS } from "cfg/const";
import { SessionDataResult, UserSessionsResultItem, Word } from "interfaces/services";
import { Display, Person } from "interfaces/db";
import { back, singleCompareFunc, sort } from "core";
import { getServicesManager } from "services";
import { durationToString } from "time";

import { NavButton } from "components/Button";
import { LabelProps, withHoverLabel } from "components/Label";

import { ReactComponent as SlowIcon } from './slow-outline.svg';
import { ReactComponent as StopIcon } from './stop-outline.svg';
import { ReactComponent as TriggerIcon } from './trigger-outline.svg';


const _BLOCK_SEPARATION_THRESHOLD_SEC = 2
const _SEC_TO_REM = 1 / 1.85

export type SessionDetailsProps = {
  item: UserSessionsResultItem,
  gotoPageView: () => void
  gotoPreviousItem: () => void
  gotoNextItem: () => void
  hasPreviousItem: boolean
  hasNextItem: boolean
}

type SessionDetailsState = {
  data?: SessionDataResult
  userBlocks?: TranscriptBlock[]
  otherBlocks?: TranscriptBlock[]
  error?: boolean
}

type TranscriptBlock = {
  start_time: number,
  end_time: number,
  text: string,
  key: string,
  personName: string,
}


export class SessionDetails extends React.Component<SessionDetailsProps, SessionDetailsState> {
  _userRef: RefObject<HTMLDivElement> = React.createRef()
  _otherRef: RefObject<HTMLDivElement> = React.createRef()

  constructor(props: SessionDetailsProps) {
    super(props)
    this.state = {}
  }

  async _maybeUpdateData(): Promise<undefined> {
    // exit early if no update is required
    const item = this.props.item    
    const stateData = this.state.data
    if (stateData != null && stateData.session.session_id === item.session_id) return

    // get data
    const data = await getServicesManager().getSessionData(item.session_id)

    // if the endpoint failed, set error
    if (data == null) {
      this.setState((_, props) => {
        // check update is current
        if (props.item.session_id !== item.session_id) return null
        return {sessionId: item.session_id, error: true}
      })
      return
    }

    // otherwise update blocks
    const update = this._dataToSessionDetailsState(data)
    this.setState((_state, props) => {
      // check update is current
      if (props.item.session_id !== item.session_id) return null
      return update
    })
  }

  _dataToSessionDetailsState(data: SessionDataResult): SessionDetailsState {
    // get a map from party code to person
    const partyCodeToPerson: Map<number, Person> = new Map(data.party_people.map(([party, person]) => [party.party_code, person]))

    // get a map from party to words
    const partyToWords: Map<number, Word[]> = new Map()
    for (let i = 0; i < data.transcripts.length; i ++) {
      // ensure the transcript party is known
      const transcript = data.transcripts[i]
      if (!partyToWords.has(transcript.party_code))
        partyToWords.set(transcript.party_code, [])
      const words = partyToWords.get(transcript.party_code) as Word[]

      // collect words
      words.push(...data.transcript_data[i])
    }

    // collect transcript blocks from users and others
    const userBlocks: TranscriptBlock[] = []
    const otherBlocks: TranscriptBlock[] = []
    for (const [party_code, words] of partyToWords.entries()) {
      const wordBlocks: Word[][] = []

      // iterate over party words in ascending order
      const sortedWords = sort(words, true, (x, y) => singleCompareFunc(x.start_time, y.start_time))
      for (let j = 0; j < sortedWords.length; j++) {
        const word = words[j]
        // determine spacing from the last word in the previous block
        const separation = wordBlocks.length === 0 ? Infinity : word.start_time - back(back(wordBlocks)).end_time
        if (separation > _BLOCK_SEPARATION_THRESHOLD_SEC)
          wordBlocks.push([word])
        else {
          const wordBlock = back(wordBlocks)
          wordBlock.push(word)
        }
      }

      // convert word blocks to transcript blocks
      let person = partyCodeToPerson.get(party_code)
      const personName = person == null ? 'Unknown' : person.person_name
      const transcriptBlocks: TranscriptBlock[] = wordBlocks.map((x, i) => ({
        start_time: x[0].start_time,
        end_time: back(x).end_time,
        text: x.map((y) => y.text).join(' '),
        personName: personName,
        key: `${party_code} ${i}`,
      }))

      // extend either user or other
      if (person != null && person.person_id === data.session.person_id)
        userBlocks.push(...transcriptBlocks)
      else
        otherBlocks.push(...transcriptBlocks)
    }

    // sort and return
    return {
      data: data,
      userBlocks: sort(userBlocks, true, (x, y) => singleCompareFunc(x.start_time, y.start_time)), 
      otherBlocks: sort(otherBlocks, true, (x, y) => singleCompareFunc(x.start_time, y.start_time)),
      error: false,
    }
  }

  componentDidMount(): void {
    this._maybeUpdateData()
  }

  _realign(): void {
    // realign positions
    const remToPx = parseFloat(getComputedStyle(document.documentElement).fontSize.replace('px', ''))
    for (const ref of [this._userRef, this._otherRef]) {
      const root = ref.current
      if (root == null) continue

      // get emperical height
      const children = Array.from(root.children)
      const heights: number[] = children.map((x) => x.getBoundingClientRect()['height'] / remToPx)

      // get target midpoint
      const midpoints: number[] = children.map((x) => parseFloat(x.getAttribute('data-midpoint') ?? '') * _SEC_TO_REM)

      // get tops
      const tops: number[] = []
      for (let i = 0; i < children.length; i++) {
        const priorBottom = i === 0 ? 0 : tops[i - 1] + heights[i - 1]
        tops.push(Math.max(priorBottom, midpoints[i] - heights[i] / 2))
        children[i].setAttribute('style', `top: ${tops[i]}rem`)
      }

      // set an overall height on the element
      if (tops.length > 0)
        root.setAttribute('style', `height: ${back(tops) + back(heights)}rem`)
    }
  }

  /**
   * After state changes try to update data 
   */
  componentDidUpdate(): void {
    this._maybeUpdateData()
    this._realign()
  }

  _isUserToBgColorClass(isUser: boolean): string {
    return isUser ? 'bg-cyan-400' : 'bg-fuchsia-400'
  }

  _isUserToLightBgColorClass(isUser: boolean): string {
    return isUser ? 'bg-cyan-100' : 'bg-fuchsia-100'
  }

  _renderHeader(data: SessionDataResult): JSX.Element {
    const item = this.props.item

    // create an element for dispositions
    const dispositionItems = data.dispositions.map((x) => (
      <div key={x.label} className="bg-slate-100 rounded-md text-sm px-2 py-1">{x.disposition}</div>
    ))
    const dispositionElement = (
      <div className="flex m-2 gap-2 items-center">
        <div className="font-bold">Dispositions:</div>
        <div className="flex gap-2">{dispositionItems}</div>
      </div>
    )

    // create an element for notes
    const notes = (data.note ?? {}).note ?? ''
    const notesElement = (
      <div className="flex m-2 gap-2 items-center">
        <div className="font-bold">Notes:</div>
        <div className="text-sm bg-slate-100 px-2 py-1 rounded-md">{notes}</div>
      </div>
    )

    // form header
    return (
      <div>
        <div className="text-lg">Conversation with {item.person_name}</div>
        {dispositionItems.length > 0 && dispositionElement}
        {notes !== '' && notesElement}
      </div>
    )
  }

  /**
   * Create an element containing time bars
   */
  _renderTimeBars(): JSX.Element {
    const bars: JSX.Element[] = []
    for (const isUser of [true, false]) {
      // get class style for user
      const className = `w-2 ${this._isUserToBgColorClass(isUser)} opacity-50 absolute`

      // make blocks
      const blocks = isUser ? this.state.userBlocks : this.state.otherBlocks
      for (const block of blocks ?? []) {
        const style = {
          top: `${block.start_time * _SEC_TO_REM}rem`, 
          height: `${(block.end_time - block.start_time) * _SEC_TO_REM}rem`
        }
        bars.push(<div className={className} style={style} key={block.key}></div>)
      }
    }
    return <div className="w-2 relative bg-zinc-200">{bars}</div>
  }

  _renderTimeLabels(): JSX.Element {
    const item = this.props.item
    const duration = item.start == null || item.end == null ? 0 : item.end.getTime() - item.start.getTime()
    const children: JSX.Element[] = []
    for (let x = 0; x < duration; x += 15 * SEC_TO_MS)
      children.push(<div className='absolute' style={{top: `${x / SEC_TO_MS * _SEC_TO_REM - .5}rem`, right: 0}} key={x}>{durationToString(x)}-</div>)
    return <div className="relative w-8 text-xs text-slate-500">{children}</div>
  }

  /**
   * Create an element showing prompts 
   */
  _renderPrompts(): JSX.Element {
    const item = this.props.item
    const displays = (this.state.data ?? {}).displays ?? []
    const children: JSX.Element[] = []
    for (const display of displays) {
      // only show prompts with an icon
      const displayElement = displayToMaybeElement(display)
      if (displayElement == null) continue

      // create child node
      const key = `${display.client_id} ${display.prompt_id}`
      const top = (display.start.getTime() - item.scheduled_start.getTime()) * MS_TO_SEC * _SEC_TO_REM
      children.push((
        <div className="absolute" style={{top: `${top}rem`, left: '-.75rem'}} key={key}>
          {displayElement}
        </div>
      ))
    }
    return <div className="relative">{children}</div>
  }

  _renderTextBlocks(blocks: TranscriptBlock[], isUser: boolean): JSX.Element {
    const children: JSX.Element[] = []
    const direction = isUser ? 'items-end right-0' : 'items-start left-0'
    const className = `${this._isUserToLightBgColorClass(isUser)} rounded-md absolute px-2 py-1 flex flex-col ${direction}`

    for (const block of blocks) {
      const style = {top: `${block.start_time * _SEC_TO_REM}rem`}
      const midpoint = (block.end_time + block.start_time) / 2
      const child = (
        <div className={className} style={style} key={block.key} data-midpoint={midpoint}>
          {block.text}
        </div>
      )
      children.push(child)
    }

    const ref = isUser ? this._userRef : this._otherRef
    return <div className="relative flex-grow text-sm" ref={ref}>{children}</div>
  }

  _renderBody() {
    // show placeholder while loading
    const item = this.props.item
    const data = this.state.data
    if (data == null || item.session_id !== data.session.session_id)
      return <div>Loading...</div>

    // show error message
    if (this.state.error)
      return <div>Error loading session data</div>

    return (
      <Fragment>
        {this._renderHeader(data)}
        <div className="flex py-4">
          {this._renderTextBlocks(this.state.otherBlocks ?? [], false)}
          <div className="flex mx-3">
            {this._renderTimeLabels()}
            {this._renderTimeBars()}
            {this._renderPrompts()}
          </div>
          {this._renderTextBlocks(this.state.userBlocks ?? [], true)}
        </div>
      </Fragment>
    )
  }

  render(): JSX.Element {
    return (
      <div className="h-full">
        {this._renderBody()}
        <div className="flex gap-4 bottom-10 absolute right-10">
          <NavButton enabled={true} active={false} label={'↺'} onClick={() => this.props.gotoPageView()}></NavButton>
          <NavButton enabled={this.props.hasPreviousItem} active={false} label={'↩'} onClick={this.props.gotoPreviousItem}></NavButton>
          <NavButton enabled={this.props.hasNextItem} active={false} label={'↪'} onClick={this.props.gotoNextItem}></NavButton>
        </div>
      </div>
    )
  }
}

function displayToMaybeElement(display: Display): ReactElement | undefined {
  switch (display.prompt_type) {
    case 'MONOLOG':
      return <Stop height="1em" width="1em" labelOrientation="right" labelHeader="Monolog"></Stop>
    case 'CADENCE_FAST':
      return <Slow height="1em" width="1em" labelOrientation="right" labelHeader="Tempo"></Slow>
    case 'TRIGGER':
      return <Trigger height="1em" width="1em" labelOrientation="right" labelHeader="Trigger" labelText={display.prompt_text ?? undefined}></Trigger>
  }
}

const Stop = withHoverLabel<React.SVGProps<SVGSVGElement> & LabelProps>(StopIcon)
const Slow = withHoverLabel<React.SVGProps<SVGSVGElement> & LabelProps>(SlowIcon)
const Trigger = withHoverLabel<React.SVGProps<SVGSVGElement> & LabelProps>(TriggerIcon)
