class ListUserSessionsServiceManager {
  apiKey: string | null = null
  hostname: string | null = null
  userIdToEndDateJsonSafeToCachedPromise: Map<string | undefined, Map<number | undefined, Promise<UserSessionsResult | null>>> = new Map()

  async getResult(
    apiKey: string, 
    hostname: string, 
    userId?: string, 
    maxStartJsonSafe?: number,
  ): Promise<UserSessionsResult | null> {
    // ensure the cache is consistent with the requested api and host
    if (this.apiKey !== apiKey || this.hostname !== hostname)
      this.userIdToEndDateJsonSafeToCachedPromise = new Map()

    // ensure the userId is in the cache map
    if (!this.userIdToEndDateJsonSafeToCachedPromise.has(userId))
      this.userIdToEndDateJsonSafeToCachedPromise.set(userId, new Map())

    // check if the cached promise includes the requested data
    const jsonSafeToCachedPromise = this.userIdToEndDateJsonSafeToCachedPromise.get(userId)!
    if (jsonSafeToCachedPromise.has(maxStartJsonSafe)) 
      return jsonSafeToCachedPromise.get(maxStartJsonSafe) ?? null

    // make a promise resolving to session data
    this.apiKey = apiKey
    this.hostname = hostname
    const cachedPromise = this._getResultFromService(apiKey, hostname, userId, maxStartJsonSafe)
    jsonSafeToCachedPromise.set(maxStartJsonSafe, cachedPromise)
    return cachedPromise
  } 

  async _getResultFromService(
    apiKey: string, 
    hostname: string, 
    userId?: string, 
    maxStartJsonSafe?: number,
  ): Promise<UserSessionsResult | null> {
    const url = `https://${hostname}/${LIST_USER_SESSIONS_ENDPOINT}`
    const parameters: Record<string, string> = {'api_key': apiKey}
    if (userId !== undefined) parameters['user_id'] = userId
    if (maxStartJsonSafe !== undefined) parameters['max_start'] = `${maxStartJsonSafe}`
    try {
      const result = await simpleGet(url, parameters)
      return jsonSafeToUserSessionResult(result)
    } catch (e) {
      logInfo(`${_LOG_SCOPE} Unexpected error from list-user-sessions endpoint`, e)
      return null
    }
  }
}

class GetSessionDataServiceManager {
  apiKey: string | null = null
  hostname: string | null = null
  sessionIdToCachedPromise: Map<string, Promise<SessionDataResult | null>> = new Map()

  async getResult(
    apiKey: string, 
    hostname: string, 
    sessionId: string, 
  ): Promise<SessionDataResult | null> {
    // ensure the cache is consistent with the requested api and host
    if (this.apiKey !== apiKey || this.hostname !== hostname) {
      this.sessionIdToCachedPromise = new Map()
      this.apiKey = apiKey
      this.hostname = hostname
    }

    // check if the cached promise includes the requested data
    if (this.sessionIdToCachedPromise.has(sessionId)) 
      return this.sessionIdToCachedPromise.get(sessionId)!

    // make a promise resolving to session data
    const cachedPromise = this._getResultFromService(apiKey, hostname, sessionId)
    this.sessionIdToCachedPromise.set(sessionId, cachedPromise)
    return cachedPromise
  } 

  async _getResultFromService(
    apiKey: string, 
    hostname: string, 
    sessionId: string,
  ): Promise<SessionDataResult | null> {
    const url = `https://${hostname}/${GET_SESSION_DATA_ENDPOINT}`
    const parameters: Record<string, string> = {'api_key': apiKey, 'session_id': sessionId}
    try {
      const result = await simpleGet(url, parameters)
      return jsonSafeToSessionDataResult(result)
    } catch (e) {
      logInfo(`${_LOG_SCOPE} Unexpected error from list-user-sessions endpoint`, e)
      return null
    }
  }
}

class TriggerServiceManager {
  apiKey: string | null = null
  hostname: string | null = null
  triggerToCachedPromise: Promise<TriggerListDataResult | null> | null = null
  async getResult(
    apiKey: string, 
    hostname: string, 
  ): Promise<TriggerListDataResult | null> {
    // ensure the cache is consistent with the requested api and host
    if (this.apiKey !== apiKey || this.hostname !== hostname) {
      this.triggerToCachedPromise = null
      this.apiKey = apiKey
      this.hostname = hostname
    }

    // check if the cached promise includes the requested data
    if (this.triggerToCachedPromise !== null) return this.triggerToCachedPromise

    // make a promise resolving to session data
    this.apiKey = apiKey
    this.hostname = hostname
    const cachedPromise = this._getResultFromService(apiKey, hostname)
    this.triggerToCachedPromise = cachedPromise
    return cachedPromise
  } 

  async _getResultFromService(
    apiKey: string, 
    hostname: string, 
  ): Promise<TriggerListDataResult | null> {
    const url = `https://${hostname}/${GET_USER_TRIGGERS_ENDPOINT}`
    const parameters: Record<string, string> = {'api_key': apiKey}
    try {
      const result = await simpleGet(url, parameters)
      return jsonSafeToTriggerListDataResult(result)
    } catch (e) {
      logInfo(`${_LOG_SCOPE} Unexpected error from get user triggers endpoint`, e)
      return null
    }
  }

  removeCachedTrigger(): void {
    this.triggerToCachedPromise = null 
  }


  async postParams(
    apiKey: string, 
    hostname: string, 
    triggerParams: TriggerListDataResult,
  ): Promise<Record<string, any> | null> {
    this.apiKey = apiKey
    this.hostname = hostname
    const promiseResult = this._postParamsFromService(apiKey, hostname, triggerParams)
    return promiseResult
  } 

  async _postParamsFromService(
    apiKey: string, 
    hostname: string,
    triggerParams: TriggerListDataResult,
  ): Promise<Record<string, any> | null> {
    const url = `https://${hostname}/${SAVE_USER_TRIGGERS_ENDPOINT}`
    const parameters: Record<string, any> = {'triggers': triggerParams['triggers'], 'trigger_formulas': triggerParams['trigger_formula'], 'api_key': apiKey}

    try {
      const result = await simplePost(url, parameters)
      if (result['success']) this.removeCachedTrigger()
      return result
    } catch (e) {
      logInfo(`${_LOG_SCOPE} Unexpected error from get user triggers endpoint`, e)
      return null
    }
  }

}


let _SERVICES_MANAGER = new ServicesManager()
export function getServicesManager(): ServicesManager {
  return _SERVICES_MANAGER
}
