import React, { RefObject } from 'react';

import { SessionList, SessionListProps } from 'components/SessionList'
import { SessionDetails, SessionDetailsProps } from 'components/SessionDetails';
import { UserSessionsResultItem } from 'interfaces/services';
import { getServicesManager } from 'services';
import { maybeBack } from 'core';

type HistoryState = {
  // state associated with loaded data
  items?: UserSessionsResultItem[]
  continueMaxStartJsonSafe?: number | null,

  // state associated with what the user is viewing
  viewingItemIdx: number // both items and pages are zero indexed
  isViewingItem: boolean   // true if the user is currently viewing a single item
  itemsPerPage: number
  eventListenerAdded: boolean
}

export class History extends React.Component<{}, HistoryState> {

  _listRef: RefObject<HTMLDivElement> = React.createRef()
  
  state: HistoryState = {
    viewingItemIdx: 0,
    isViewingItem: false,
    itemsPerPage: 20,
    eventListenerAdded: false,
  }

  _gotoPageViewBound = this._gotoPageView.bind(this)
  _gotoItemViewBound = this._gotoItemView.bind(this)
  _gotoPreviousItemBound = this._gotoPreviousItem.bind(this)
  _gotoNextItemBound = this._gotoNextItem.bind(this)
  _onScrollListenerBound = this._onScrollListener.bind(this)

  _getUserId(): string | undefined {
    const userId = (new URLSearchParams(window.location.search)).get('userId')
    return userId ? userId : undefined
  }

  /**
   * Asynchronously extend the known conversations if appropriate.
   */
   async _maybeExtendConversations(isKnownEndOfScroll? : boolean): Promise<undefined> {
    // exit early if the current items are consistent with the requested page and are complete
    const items = this.state.items
    const continueMaxStartJsonSafe = this.state.continueMaxStartJsonSafe
    const stateIsValid = items !== undefined && continueMaxStartJsonSafe !== undefined
    const hasMoreItems = stateIsValid && continueMaxStartJsonSafe !== null
    const hasEnoughItems = stateIsValid && ((items.length > this.state.viewingItemIdx + this.state.itemsPerPage) && !isKnownEndOfScroll)
    
    if (stateIsValid && (hasEnoughItems || !hasMoreItems)) return

    // formulate request
    const pageUserId = this._getUserId()
    const maxStartJsonSafe = hasMoreItems ? continueMaxStartJsonSafe : undefined
    
    // get new items
    const result = await getServicesManager().getListUserSessions(pageUserId , maxStartJsonSafe)

    // there is no update if the service call failed
    if (result == null) return

    // update state
    this.setState((state) => {
      // get old items to retain. note: results and continuation is in _descending_ time order
      const lastStateItems = state.items
      const retainItems = lastStateItems !== undefined ? lastStateItems : []
      const lastItemStart = (maybeBack(retainItems) ?? {}).scheduled_start
      const newItems = lastItemStart == null ? result.items : result.items.filter((x) => x.scheduled_start < lastItemStart)

      // exit early if there is no state change. note: this is important to prevent infinite looping
      if (newItems.length === 0 && state.continueMaxStartJsonSafe === result.continueFromJsonSafe)
        return null

      // make update
      return {
        items: [...retainItems, ...newItems],
        continueMaxStartJsonSafe: result.continueFromJsonSafe,
      }
    })
  }



  /**
   * Set the viewingItemIdx to the previous page
   */
  _gotoPageView() {
    this.setState({isViewingItem: false})
  }

  /**
   * Switch between viewing a list of items and a single item
   */
  _gotoItemView(viewingItemIdx: number): void {
    this.setState({isViewingItem: true, viewingItemIdx: viewingItemIdx})
  }

  /**
   * Set the viewingItemIdx to the previous item
   */
   _gotoPreviousItem() {
    this.setState((state) => ({viewingItemIdx: state.viewingItemIdx - 1}))
  }

  /**
   * Set the viewingItemIdx to the next item
   */
  _gotoNextItem() {
    this.setState((state) => ({viewingItemIdx: state.viewingItemIdx + 1}))
  }



  /**
   * Start loading data on mount
   */
  componentDidMount(): void {
    this._maybeExtendConversations()
  }

  /**
   * After state changes try to update conversations
   */
  componentDidUpdate(): void {
    this._maybeExtendConversations()
    if (this._listRef.current !== null && !this.state.eventListenerAdded) {
      this._listRef.current.addEventListener('scroll', this._onScrollListenerBound)
      this.setState({eventListenerAdded: true})
    }
  }

  _onScrollListener() : void {
    if (this._listRef.current!.scrollTop + this._listRef.current!.clientHeight >=
      this._listRef.current!.scrollHeight) {
      this._maybeExtendConversations(true)
    }
  }
  /**
   * Render either a list view or a details view
   */
  render(): JSX.Element {
    // show a loading message until there are items
    const items = this.state.items
    if (items == null) return <div>Loading History...</div>

    const sessionListProps: SessionListProps = {
      items: items,
      gotoItemView: this._gotoItemViewBound,
      isExhausted: this.state.continueMaxStartJsonSafe === null,
      isViewingItem: this.state.isViewingItem, 
      viewingItemIdx: this.state.viewingItemIdx
    }

    let transcriptElement: JSX.Element 

    if (this.state.isViewingItem) {
      const sessionDetailsProps: SessionDetailsProps = {
        item: items[this.state.viewingItemIdx],
        gotoPageView: this._gotoPageViewBound,
        gotoPreviousItem: this._gotoPreviousItemBound,
        gotoNextItem: this._gotoNextItemBound,
        hasPreviousItem: this.state.viewingItemIdx > 0,
        hasNextItem: this.state.viewingItemIdx < items.length - 1,
      } 

      transcriptElement = (
        <div className='w-full h-screen overflow-y-auto ml-2 mr-2'>
          <SessionDetails {...sessionDetailsProps}></SessionDetails>
        </div>
      )
    } else {
      transcriptElement = ( 
        <div className='w-full h-screen overflow-y-auto ml-2 mr-2'>
          Click to view a transcript!
        </div>
      )
    }

    return (
      <div className='flex flex-column overflow-y-auto w-full'>
      <div className='h-screen overflow-y-auto min-w-fit w-fit' ref={this._listRef}>
        <SessionList {...sessionListProps}></SessionList>
      </div>
      {transcriptElement}
      </div>
    )
  }
}
