import React, { RefObject } from 'react';

import { dateToString, durationToString } from 'time';

import { Table, TableProps } from 'components/Table'
import { UserSessionsResultItem } from 'interfaces/services';

export type SessionListProps = {
  items: UserSessionsResultItem[],
  gotoItemView: (itemIdx: number) => void,
  isExhausted: boolean
  isViewingItem: boolean
  viewingItemIdx: number
  // hasMore: boolean  
}

type SessionListState = {
  listenerAttached: boolean
}

export class SessionList extends React.Component<SessionListProps, SessionListState> {
  _listRef: RefObject<HTMLDivElement> = React.createRef()

  constructor (props: SessionListProps) {
    super(props)
    this.state = {
      listenerAttached: false,
    }
  }

  getCompressedTableProps(): TableProps<UserSessionsResultItem>  {
    // properties used by table
    const tableProps: TableProps<UserSessionsResultItem> = {
      headers: ['Prospect', 'Duration'],
      keyFunc: (item: UserSessionsResultItem): string => item.session_id,
      columnFuncs: [
        (item: UserSessionsResultItem) => item.person_name,
        (item: UserSessionsResultItem) => durationToString(item.start == null || item.end == null ? 0 : (item.end.getTime() - item.start.getTime())),
      ],
      items: this.props.items,
      onRowClick: (_item: UserSessionsResultItem, i: number): void => 
         {
          this.props.gotoItemView(i)
         },
      highlightedItemIdx: this.props.viewingItemIdx
    }

    return tableProps
  }
  
  getFullTableProps(): TableProps<UserSessionsResultItem>  {
    // properties used by table
    const tableProps: TableProps<UserSessionsResultItem> = {
      headers: ['Prospect', 'Time', 'Duration', 'Disposition'],
      keyFunc: (item: UserSessionsResultItem): string => item.session_id,
      columnFuncs: [
        (item: UserSessionsResultItem) => item.person_name,
        (item: UserSessionsResultItem) => dateToString(item.scheduled_start),
        (item: UserSessionsResultItem) => durationToString(item.start == null || item.end == null ? 0 : (item.end.getTime() - item.start.getTime())),
        (item: UserSessionsResultItem) => item.dispositions ?? '',
      ],
      items: this.props.items,
      onRowClick: (_item: UserSessionsResultItem, i: number): void =>  {
        this.props.gotoItemView(i)
      },
      highlightedItemIdx: -1
    }

    return tableProps
  }


  render(): JSX.Element {
  let tableProps: TableProps<UserSessionsResultItem>
    if (this.props.isViewingItem) {
     tableProps = this.getCompressedTableProps()
    } else {
      tableProps = this.getFullTableProps()
    }

  return (
    
      <div className='flex flex-col items-end mr-1 h-full'>
        <Table {...tableProps}></Table>
      </div> 
  )
  }
}