import React from 'react';

export type TriggerPromptsProps = {
  prompts: string[]
  addPrompt: () => void,
  updatePrompt: (idx: number, updatedPrompt: string) => void,
  removePrompt: (idx: number) => void,
}

export class TriggerPrompts extends React.Component<TriggerPromptsProps, {}> {  

  constructor(props: TriggerPromptsProps) {
    super(props)
  }

  renderInput(prompt: string, idx: number): JSX.Element {
    return (<div className='mt-2 flex flex-row align-center content-center justif-center'>
      <input onChange={(e) => this.props.updatePrompt(idx, e.target.value)} value={prompt} className='rounded-xl p-1 mr-2'></input>
      <div key={idx} data-key={idx} onClick={(e => this.props.removePrompt(idx))} className='m-auto cursor-pointer bg-black text-white w-6 h-6 flex justify-center content-center items-center rounded-2xl'>X</div>
    </div>)
  }

  render(): JSX.Element {
      return (<div>
        {this.props.prompts.map((prompt, idx) =>{
         return (<div key={idx}>{this.renderInput(prompt, idx)}</div>)
        })}
        <div onClick={this.props.addPrompt} className='mt-3 ml-auto cursor-pointer bg-lime-900 text-white w-6 h-6 flex justify-center content-center items-center rounded-2xl font-bold'>+</div>
      </div>
      )
  }
}