import { TriggerFormulaProps, TriggerFormulas } from 'components/TriggerFormulas';
import { TriggerPrompts, TriggerPromptsProps } from 'components/TriggerPrompts';
import React from 'react'

export type TriggerDefinitionProps = {
    label: string,
    targetIsUser: boolean,
    formulaProps: TriggerFormulaProps,
    promptsProps: TriggerPromptsProps,
    removeTrigger: () => void, 
    updateLabel: (updatedLabel: string) => void,
    setTargetIsUser: (targetIsUser: boolean) => void,
}

export class TriggerDefinition extends React.PureComponent<TriggerDefinitionProps, {}> {  
    _youRadio: React.RefObject<HTMLInputElement>;
    _prospectRadio: React.RefObject<HTMLInputElement>;

    constructor (props: TriggerDefinitionProps) {
        super(props)
        this._youRadio = React.createRef();
        this._prospectRadio = React.createRef();
    }

    _handleRadioInput(targetIsUser: boolean) {
        console.log(targetIsUser)
        this._youRadio.current!.checked = targetIsUser
        this._prospectRadio.current!.checked = !targetIsUser
        this.props.setTargetIsUser(targetIsUser)
    }

    render(): JSX.Element {
        return (
            <div className="m-8 p-8 rounded-xl bg-gray-300">
            <div className="mb-3 flex flex-row justify-between items-center content-center w-full">
            <div className="ml-3 mr-3">
                <label htmlFor="name"><b>Label:</b></label>
                <input onChange={(e) => this.props.updateLabel(e.target.value)} value={this.props.label} className="text p-2 ml-4 rounded-xl" name="target" data-trigger-node-id="trigger-name"></input>
            </div>

            <div onClick={this.props.removeTrigger} className='cursor-pointer bg-black text-white w-6 h-6 flex justify-center content-center items-center rounded-2xl'>X</div>
            </div>
        
            <div className="mt-2 ml-3 mr-3 flex items-center content-center justify-start">

            <b>Speaker:</b> 
            <div className="flex flex-row items-center content-center justify-evenly">
                <div className='ml-2'>
                <label htmlFor='you'>You</label>
                <input ref={this._youRadio} onChange={(e) => this._handleRadioInput(e.target.checked)} checked={this.props.targetIsUser} className="ml-1" type="radio" id="you" value="you" data-trigger-node-id="triggers-on-you"></input>
                </div>
                <div className='ml-2'>
                <label htmlFor="prospect">Prospect</label>
                <input ref={this._prospectRadio} onChange={(e) => this._handleRadioInput(!e.target.checked)} checked={!this.props.targetIsUser} className="ml-1" type="radio" id="other" value="other" data-trigger-node-id="triggers-on-prospect"></input>
                </div>
            </div>
            </div>  

            {/* onChange={(e) => this.props.updatePrompt(idx, e.target.value)} */}
        
            <div className="mt-2 w-full ml-3 mr-3 flex">
            <div className="pr-2">
                <b>Displayed Prompt</b>
                <TriggerPrompts {...this.props.promptsProps}></TriggerPrompts>
            </div>
        
            <div className="padding: 0 0 0 8px">
                <b>Trigger</b>
                <TriggerFormulas {...this.props.formulaProps}></TriggerFormulas>
            </div>
            </div>
        </div>

        
        )
  }
}