import React from 'react';

import { NavLink } from "react-router-dom";
import { Page } from 'cfg/pages'


export class SideBar extends React.Component<{page: Page, setPage: (page: Page) => void}, {}> {  
  _makeOption(label: string, target: string, imagePath: string): JSX.Element {
    return (                             
    <NavLink to={`/${target}`} style={({isActive}) => (isActive ? {backgroundColor: 'blue', opacity: 1} : {opacity: 0.8})}>
    <li className="flex items-center p-2 space-x-3 rounded-md bg-inherit">
        <img src={imagePath}/> 
        <span className="text-gray-100">
        {label}
        </span>
    </li>
    </NavLink>
    )
  }
  
  render(): JSX.Element {
    return (
        <div className="flex">
            <div className="h-screen flex flex-col p-3 pb-5 bg-gray-800 shadow w-40">
                <div className="space-y-3 flex flex-col justify-between h-full">
                    <div>
                    <div className="flex items-center">
                        <h2 className="text-xl font-bold text-white opacity-1">Dashboard</h2>
                    </div>                    <div className="flex-1">
                        <ul className="pt-2 pb-4 space-y-1 text-sm">
                        {this._makeOption('Analytics', 'analytics', 'analytics_icon.svg')}
                        {this._makeOption('History', 'history', 'history_icon.svg')}
                        {this._makeOption('Triggers', 'triggers', 'triggers_icon.svg')}
                        </ul>
                    </div>
                    </div>
                    <img className='mb-14 ml-3 w-2/3'src='logo.svg'/> 

                </div>
            </div>
        </div>
    );
  }
}