export function makeColors(number: number, light: boolean = true): string[] {
    // this is the default from seaborn
    // see: https://seaborn.pydata.org/tutorial/color_palettes.html
    // todo: consider equally spaced samples of a perceptually uniform palette instead of wrapping
    let palette: string[]
    if (light)
        palette = ['#a1c9f4', '#ffb482', '#8de5a1', '#ff9f9b', '#d0bbff', '#debb9b', '#fab0e4', '#cfcfcf', '#fffea3', '#b9f2f0']
    else
        palette = ['#4c72b0', '#dd8452', '#55a868', '#c44e52', '#8172b3', '#937860', '#da8bc3', '#8c8c8c', '#ccb974', '#64b5cd']
    return Array(number).fill(null).map((_, i) => palette[i % palette.length])
}