import { TriggerDefinition, TriggerDefinitionProps } from "components/TriggerDefinition";
import { groupByKeys } from "core";
import React from "react";
import { getServicesManager } from "services";
import uuid from 'react-uuid';
import { TriggerDataResult, TriggerFormulaDataResult, TriggerListDataResult } from "interfaces/services";
import { Alert, AlertProps } from "components/Alert";

export type TriggerState = {
    label: string,
    targetIsUser: boolean,
    prompts: string[],
    formulas: string[]
}

export type TriggerListState = {
  showAlert?: boolean
  alertProps?: AlertProps 
  triggers?: Map<string, TriggerState>
}

export class Trigger extends React.Component<{}, TriggerListState> {
  _bodyRef: React.RefObject<HTMLInputElement> = React.createRef();

  /**
 * Asynchronously extend the known conversations if appropriate.
 */
  async _fetchTriggers(): Promise<undefined> {
    // get new items
    const result = await getServicesManager().getTriggerData()

    // there is no update if the service call failed
    if (result == null) return
    // group by trigger id
    const idToFormulas = groupByKeys(result['trigger_formula'], (item: TriggerFormulaDataResult, idx: number) => item['trigger_id'], (item: TriggerFormulaDataResult, idx: number) => item['trigger_formula'])
    const triggers = new Map<string, TriggerState>()

    result['triggers'].map((trigger) => {
      triggers.set(trigger['trigger_id'], {
        'label': trigger['trigger_name'],
        'targetIsUser': trigger['triggers_on_user'],
        'prompts': trigger['trigger_prompt'].split('\n'),
        'formulas': idToFormulas.get(trigger['trigger_id']) ?? []
      })
    })

    this.setState({ 'triggers': triggers })
  }


  async saveTriggers(): Promise<undefined> {
    return
  }

  _addTrigger() {
    let triggers = this.state.triggers
    if (triggers === undefined) {
      triggers = new Map<string, TriggerState>()
    }
    triggers.set(uuid(), {'label': '', 'targetIsUser': false, prompts: [''], formulas: ['']})

    this.setState(() => ({
      triggers: triggers,
    }))

    setTimeout(() => {
      this._bodyRef.current?.scrollIntoView({block: "end", behavior: 'smooth'});
    }, 100);
  }

  _onAlertClose() {
    this.setState({
      showAlert: false
    })
  }

  async _onSave() {
    let triggers: TriggerDataResult[] = []
    let formulas: TriggerFormulaDataResult[] = []

    this.state.triggers?.forEach((triggerDefinition: TriggerState, key: string) => {
      triggers.push(
        {'trigger_id': key, 
        'trigger_name': triggerDefinition['label'], 
        'trigger_prompt': triggerDefinition['prompts'].join('\n'),
        'triggers_on_user': triggerDefinition['targetIsUser']
      })

      triggerDefinition['formulas'].forEach ((formula: string) => {
        formulas.push({
          'trigger_id': key,
          'trigger_formula': formula
        })
      })
    })

    const triggerParams: TriggerListDataResult = {
      'triggers': triggers,
      'trigger_formula': formulas
    }

    const result = await getServicesManager().postTriggerData(triggerParams)
    if (result === null || !result['success']) {
      this.setState({
        showAlert: true,
        alertProps: {
          bgColorTailwind: 'bg-red-700',
          stringToShow: 'Failure: triggers not saved. Please retry at a later time',
          onClose: this._onAlertClose.bind(this)
        }
      })
    }
    else {
      this.setState({
        showAlert: true,
        alertProps: {
          bgColorTailwind: 'bg-green-700',
          stringToShow: 'Sucess - triggers saved!',
          onClose: this._onAlertClose.bind(this)
        }
      })
    }

    setTimeout(() => {
      this.setState({
        showAlert: false,
        alertProps: undefined
      })
    }, 3000);

  }

  /**
 * Start loading data on mount
 */
    componentDidMount(): void {
    this._fetchTriggers()
  }

  render(): JSX.Element {
    
    if (this.state == undefined || this.state.triggers === undefined) return (<div>Loading Triggers...</div>) 
    
    const triggerDefinitions: TriggerDefinitionProps[] = []
    this.state.triggers.forEach((triggerDefinition: TriggerState, key: string) => {
      const removeTriggerBound = () => {
        this.setState((prevState) => {
          const triggers = new Map(Array.from(prevState.triggers!.entries()).filter(([k, v]) => k != key))
          return {
            triggers: triggers
          }
        })
      }

      const addFormulaBound = () => {
        this.setState((prevState) => {
          const triggers = new Map(prevState.triggers)
          const triggerState = triggers.get(key)
          if (triggerState == undefined) throw new Error("key not found for trigger")
          const updatedEntry: TriggerState = {
            ...triggerState,
            formulas: [...triggerState['formulas'], ""]
          }

          return {
            triggers: triggers.set(key, updatedEntry)
          }
        })
      }

      const updateFormulaBound = (idx: number, updatedFormula: string) => {
        this.setState((prevState) => {
          const triggers = new Map(prevState.triggers)
          const triggerState = triggers.get(key)
          if (triggerState == undefined) throw new Error("key not found for trigger")
          const updatedEntry: TriggerState = {
            ...triggerState,
            formulas: [...triggerState["formulas"].slice(0, idx), updatedFormula, ...triggerState["formulas"].slice(idx + 1)]
          }

          return {
            triggers: triggers.set(key, updatedEntry)
          }
        })
      }

      const removeFormulaBound = (idx: number) => {
        this.setState((prevState) => {
          const triggers = new Map(prevState.triggers)
          const triggerState = triggers.get(key)
          if (triggerState == undefined) throw new Error("key not found for trigger")
          const updatedEntry: TriggerState = {
            ...triggerState,
            formulas: triggerState["formulas"].filter((_, i) => i !== idx)
          }

          return {
            triggers: triggers.set(key, updatedEntry)
          }
        })
      }

      const addPromptBound = () => {
        this.setState((prevState) => {
          const triggers = new Map(prevState.triggers)
          const triggerState = triggers.get(key)
          if (triggerState == undefined) throw new Error("key not found for trigger")
          const updatedEntry: TriggerState = {
            ...triggerState,
            prompts: [...triggerState['prompts'], ""]
          }

          return {
            triggers: triggers.set(key, updatedEntry)
          }
        })
      }

      const updatePromptBound = (idx: number, updatedFormula: string) => {
        this.setState((prevState) => {
          const triggers = new Map(prevState.triggers)
          const triggerState = triggers.get(key)
          if (triggerState == undefined) throw new Error("key not found for trigger")
          const updatedEntry: TriggerState = {
            ...triggerState,
            prompts: [...triggerState["prompts"].slice(0, idx), updatedFormula, ...triggerState["formulas"].slice(idx + 1)]
          }

          return {
            triggers: triggers.set(key, updatedEntry)
          }
        })
      }

      const removePromptBound = (idx: number) => {
        this.setState((prevState) => {
          const triggers = new Map(prevState.triggers)
          const triggerState = triggers.get(key)
          if (triggerState == undefined) throw new Error("key not found for trigger")
          const updatedEntry: TriggerState = {
            ...triggerState,
            prompts: triggerState["prompts"].filter((_, i) => i !== idx)
          }

          return {
            triggers: triggers.set(key, updatedEntry)
          }
        })
      }

      const setTargetIsUserBound = (targetIsUser: boolean) => {
        this.setState((prevState) => {
          const triggers = new Map(prevState.triggers)
          const triggerState = triggers.get(key)
          if (triggerState == undefined) throw new Error("key not found for trigger")
          const updatedEntry: TriggerState = {
            ...triggerState,
            targetIsUser: targetIsUser
          }

          return {
            triggers: triggers.set(key, updatedEntry)
          }
        })
      }

      const updateLabelBound = (updatedLabel: string) => {
        this.setState((prevState) => {
          const triggers = new Map(prevState.triggers)
          const triggerState = triggers.get(key)
          if (triggerState == undefined) throw new Error("key not found for trigger")
          const updatedEntry: TriggerState = {
            ...triggerState,
            label: updatedLabel
          }

          return {
            triggers: triggers.set(key, updatedEntry)
          }
        })
      }

      triggerDefinitions.push({
          label: triggerDefinition['label'],
          targetIsUser: triggerDefinition['targetIsUser'], 
          formulaProps: {
            formulas: triggerDefinition['formulas'],
            addFormula: addFormulaBound,
            updateFormula: updateFormulaBound,
            removeFormula: removeFormulaBound,
          },
        promptsProps: {
          prompts: triggerDefinition['prompts'],
          addPrompt: addPromptBound,
          updatePrompt: updatePromptBound,
          removePrompt: removePromptBound
        },
        removeTrigger: removeTriggerBound,
        setTargetIsUser: setTargetIsUserBound,
        updateLabel: updateLabelBound
      })
    })

    return (
      
      <div className="w-full" ref={this._bodyRef}>
        {this.state.showAlert ? <Alert {...this.state.alertProps!}></Alert> : null}
        {triggerDefinitions.map(triggerDefinitionProps => {
          return (<TriggerDefinition {...triggerDefinitionProps}></TriggerDefinition>)
        })}

        <div className='ml-auto mr-9 flex bottom-5 bg-white opacity-40 p-5 rounded-xl sticky w-fit hover:opacity-100'>
        <div onClick={this._addTrigger.bind(this)} className='ml-8 cursor-pointer bg-lime-900 text-white w-52 h-6 flex justify-center content-center items-center rounded-2xl font-bold'>Add new trigger (+)</div>
        <div onClick={this._onSave.bind(this)} className='ml-6 cursor-pointer bg-violet-700 text-white w-52 h-6 flex justify-center content-center items-center rounded-2xl font-bold'>Save Triggers</div>
        </div>
      </div>
    )
  }
}
